.good
  width: 270px
  height: 380px
  border: 1px solid #DEDEDE
  display: flex
  flex-direction: column
  justify-content: space-around
  align-items: center
.image
  width: 200px
  height: 200px
.title
  width: 230px
  font-weight: 500
  font-size: 16px
  line-height: 130%
  text-align: center
  color: #000000
.price
  width: 230px
  font-weight: 700
  font-size: 18px
  line-height: 130%
  text-align: center
  color: #000000
.button,.active
  display: flex
  justify-content: center
  align-items: center
  border: none
  cursor: pointer
  background-color: #296DC1
  height: 40px
  width: 186px
  font-weight: 600
  font-size: 12px
  line-height: 130%
  letter-spacing: 0.05em
  color: #FFFFFF
.hidden
  display: none
.active
  background-color: #00A82D

@media screen and (min-width: 690px) and (max-width: 929px)
  .good
    width: 450px
    height: 590px
  .image
    width: 334px
    height: 400px
  .button
    display: flex
@media screen and (min-width: 510px) and (max-width: 689px)
  .good
    width: 450px
    height: 590px
  .image
    width: 334px
    height: 400px
  .button
    display: flex
@media screen and (min-width: 320px) and (max-width: 509px)
  .good
