.loaderCase
  width: 100%
  display: flex
  justify-content: center
.loader
  width: 100px
  height: 100px
  border-radius: 50%
  border: 3px dashed black
  animation: rotate 5s infinite linear

@keyframes rotate
  0%
    transform: rotate(0deg)
  25%
    transform: rotate(72deg)
  50%
    transform: rotate(144deg)
  75%
    transform: rotate(216deg)
  100%
    transform: rotate(360deg)